<!--OA-->
<template>
  <div class="flex">
    <div class="content body">
      <div class="box box-solid">
        <div class="box-header bg-aqua">OA</div>
        <div class="box-body" v-loading="!show">
          <div class="select-row">
            <select-times @changeTimes="changeTimes"></select-times>
            <select-alarm-type @changeType="changeType"></select-alarm-type>
            <select-status @changeStatus="changeStatus"></select-status>
            <el-button type="primary" size="mini" icon="el-icon-download" @click="exportData"></el-button>
          </div>
          <div class="table-row">
            <el-table  v-if="show" border :data="tableData" size="mini" height="100%" :default-sort = "{prop: 'alarmTime', order: 'descending'} ">
              <el-table-column type="expand">
                <template slot-scope="scope">
                  <div v-for = "item in scope.row.childrenAlarm">
                    <el-form inline>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{ item.id }}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{ item.alarmTime }}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{ item.address }}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{ item.reportContent }}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{item.leakageIndex==null ? "---" : item.leakageIndex}}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{item.totalQuantity==null ? "---" : item.totalQuantity}}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{item.addTotal==null ? "---" : item.addTotal}}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{item.addRangeTotal==null ? "---" : item.addRangeTotal}}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{item.minQuantity==null ? "---" : item.minQuantity}}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{item.addMin==null ? "---" : item.addMin}}</span>
                      </el-form-item>
                      <el-form-item class="redItem" label="">
                        <span style="color: #A0A0A0;">{{item.addRangeMin==null ? "---" : item.addRangeMin}}</span>
                      </el-form-item>
                    </el-form>
                    <el-divider></el-divider>
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" prop="id" align="center" min-width="90">
              </el-table-column>
              <el-table-column label="" prop="address" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                  <el-link type="primary" @click="link(scope.row)">{{scope.row.address}}</el-link>
                  <!--v-if="scope.row.category != '50'" <el-link v-if="scope.row.category == '50'">{{scope.row.address}}</el-link>-->
                </template>
              </el-table-column>
              <el-table-column label="" prop="alarmTime" width="140" align="center" sortable>
              </el-table-column>
              <el-table-column label="" prop="category" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                  <span v-if="scope.row.category == 10"></span>
                  <span v-if="scope.row.category == 20"></span>
                  <span v-if="scope.row.category == 30">DMA</span>
                  <span v-if="scope.row.category == 40"></span>
                  <span v-if="scope.row.category == 50"></span>
                </template>
              </el-table-column>
              <el-table-column label="" prop="leakageIndex" width="140" align="center" sortable>
                <template slot-scope="scope">
                  <el-popover
                    placement="bottom"
                    width="220"

                    trigger="hover">
                    <div class="index-message">
                      <div>(){{scope.row.totalQuantity ? scope.row.totalQuantity : '--'}}</div>
                      <div>(){{scope.row.addTotal ? scope.row.addTotal : '--'}}</div>
                      <div>{{scope.row.addRangeTotal ? scope.row.addRangeTotal : '--'}}</div>
                      <div>(){{scope.row.minQuantity ? scope.row.minQuantity : '--'}}</div>
                      <div>(){{scope.row.addMin ? scope.row.addMin : '--'}}</div>
                      <div>{{scope.row.addRangeMin ? scope.row.addRangeMin : '--'}}</div>
                    </div>
                    <el-link slot="reference" type="primary">{{scope.row.leakageIndex ? scope.row.leakageIndex : '--'}}</el-link>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column label="" prop="disposalStatus" align="center">
                <template slot-scope="scope">
                  <div :class="scope.row.disposalStatus == ''? 'yellow' : 'green'">
                    {{scope.row.disposalStatus}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" prop="dispatcher" align="center">
                <template slot-scope="scope">
                  <div :class="scope.row.dispatcher == '' ? 'yellow' : 'green'">
                    {{scope.row.dispatcher}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" prop="disposaler" align="center">
                <template slot-scope="scope">
                  <div :class="scope.row.disposaler == '' ? 'yellow' : 'green'">
                    {{scope.row.disposaler}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" prop="disposalChecker" align="center">
                <template slot-scope="scope">
                  <div :class="scope.row.disposalChecker == '' ? 'yellow' : 'green'">
                    {{scope.row.disposalChecker}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" prop="transferer" align="center">
                <template slot-scope="scope">
                  <div :class="scope.row.transferer == '' ? 'yellow' : 'green'">
                    {{scope.row.transferer}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" prop="transferDisposaler" align="center">
                <template slot-scope="scope">
                  <div :class="scope.row.transferDisposaler == '' ? 'yellow' : 'green'">
                    {{scope.row.transferDisposaler}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" prop="checker" align="center">
                <template slot-scope="scope">
                  <div :class="scope.row.checker == '' ? 'yellow' : 'green'">
                    {{scope.row.checker}}
                  </div>
                </template>
              </el-table-column>
            <el-table-column label="" align="center" width="240">
              <template slot-scope="scope">
                <el-select v-model="scope.row.disposalStatus" @change="change(scope.row)" size="mini" placeholder="" :disabled="scope.row.disposalStatus != ''">
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                </el-select>
                <el-popover
                  placement="bottom"
                  width="400"
                  trigger="click">
                  <div class="alarm-management">
                    <el-button v-if="scope.row.category != '50'" :disabled="scope.row.status" type="success" size="mini" @click="alarmHandle(scope.row)"></el-button>
                    <el-button v-if="scope.row.category != '50'" type="success" size="mini" @click="alarmPrint(1,scope.row)"></el-button>
                    <el-button v-if="scope.row.category != '50'" type="success" size="mini" @click="alarmPrint(2,scope.row)"></el-button>
                    <el-button v-if="scope.row.category == '50'" :disabled="scope.row.status" type="success" size="mini" @click="alarmHandle(scope.row)"></el-button>
                    <el-button v-if="scope.row.category == '50'" type="success" size="mini" @click="alarmPrint(3,scope.row)"></el-button>
                  </div>
                  <el-button slot="reference"  type="primary" size="mini" :disabled="scope.row.disposalStatus == '' || scope.row.disposalStatus == ''" @click="openPrint">OA</el-button>
                </el-popover>
              </template>
            </el-table-column>
            </el-table>
            <!---->
            <print-dialog :alarmId="printId" :status="status" @changePrint="changePrint" :isOpen="isOpen"></print-dialog>
            <!---->
            <survey-dispatch :userName="userName" :alarmId="dispatchFlag ? alarmId : ''" :dispatchFlag="dispatchFlag" @changeSurveyDispatch="changeSurveyDispatch"></survey-dispatch>
            <!---->
            <survey-sheet :userName="userName" :alarmId="sheetFlag ? alarmId : ''" :sheetFlag="sheetFlag" @changeSheetDispatch="changeSheetDispatch"></survey-sheet>
            <!---->
            <survey-receipt :userName="userName" :alarmId="receiptFlag ? alarmId : ''" :receiptFlag="receiptFlag" @changeSurveyReceipt="changeSurveyReceipt"></survey-receipt>
            <!---->
            <repair-transfer :userName="userName" :alarmId="repairTransferFlag ? alarmId : ''" @changeRepairTransfer="changeRepairTransfer" :repairTransferFlag="repairTransferFlag"></repair-transfer>
            <!---->
            <repair-order :userName="userName" :alarmId="repairOrderFlag ? alarmId : ''" @changeRepairOrder="changeRepairOrder" :repairOrderFlag="repairOrderFlag"></repair-order>
            <!---->
            <repair-receipt :userName="userName" :alarmId="repairReceiptFlag ? alarmId : ''" @changeRepairReceipt="changeRepairReceipt" :repairReceiptFlag="repairReceiptFlag"></repair-receipt>
            <!---->
            <fault-dispatch :userName="userName" :alarmId="faultDispatchFlag ? alarmId : ''" @changeFaultDispatch="changeFaultDispatch" :faultDispatchFlag="faultDispatchFlag"></fault-dispatch>
            <!---->
            <fault-order :userName="userName" :alarmId="faultOrderFlag ? alarmId : ''" @changeFaultOrder="changeFaultOrder" :faultOrderFlag="faultOrderFlag"></fault-order>
            <!---->
            <fault-receipt :userName="userName" :alarmId="faultReceiptFlag ? alarmId : ''" @changeFaultReceipt="changeFaultReceipt" :faultReceiptFlag="faultReceiptFlag"></fault-receipt>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import SelectTimes from "./alarmManagement/SelectTimes";
    import SelectAlarmType from "./alarmManagement/SelectAlarmType";
    import PrintDialog from "./alarmManagement/PrintDialog";
    import SurveyDispatch from "./alarmManagement/SurveyDispatch";
    import SurveySheet from "./alarmManagement/SurveySheet";
    import SurveyReceipt from "./alarmManagement/SurveyReceipt";
    import RepairTransfer from "./alarmManagement/RepairTransfer";
    import RepairOrder from "./alarmManagement/RepairOrder";
    import RepairReceipt from "./alarmManagement/RepairReceipt";
    import FaultDispatch from "./alarmManagement/FaultDispatch";
    import FaultOrder from "./alarmManagement/FaultOrder";
    import FaultReceipt from "./alarmManagement/FaultReceipt";
    import { export2Excel } from '../../js/utils'
    import SelectStatus from "./alarmManagement/SelectStatus";

    export default {
      components: {
        SelectStatus,
        FaultReceipt,
        FaultOrder,
        FaultDispatch,
        RepairReceipt,
        RepairOrder,
        RepairTransfer,
        SurveyReceipt,
        SurveySheet,
        SurveyDispatch,
        PrintDialog,
        SelectAlarmType,
        SelectTimes
      },
      name: "alarm-o-a-management",
      data() {
        return {
          // tableData: [{id: 100010,address: '',leakageIndex: '100'}],

          columns: [
            {name: '',prop: 'id'},
            {name: '',prop: 'address'},
            {name: '',prop: 'alarmTime'},
            {name: '',prop: 'reportContent'},
            {name: '',prop: 'leakageIndex'},
            {name: '',prop: 'disposalStatus'},
            {name: '',prop: 'dispatcher'},
            {name: '',prop: 'disposaler'},
            {name: '',prop: 'disposalChecker'},
            {name: '',prop: 'transferer'},
            {name: '',prop: 'transferDisposaler'},
            {name: '',prop: 'checker'},
          ],
          title: '',
          start: '',
          end: '',
          type: '',
          show: false,
          flag: true,
          isOpen: false, //
          status: 0,
          dispatchFlag: false, // 
          sheetFlag: false, //
          receiptFlag: false, //
          repairTransferFlag: false, // 
          repairOrderFlag: false, // 
          repairReceiptFlag: false, // 
          faultDispatchFlag: false, // 
          faultOrderFlag: false,  // 
          faultReceiptFlag: false,  //  
          alarmId: '', //
          printId: '',  // ID
          userName: '', // 
          name:'status',
          value:'2',
          disposalStatus: 1,
        }
      },
      created() {
        this.userName = localStorage.getItem('userName');
      },
      methods: {
        /**/
        searchAlarm() {
          this.show = false;
          let params = {
            pageNum: 1,
            pageSize: 10000,
            category: this.type,
            startTime: this.start,
            endTime: this.end,
            name: this.name,
            value: this.value,
            disposalStatus: this.disposalStatus,
          };
          let data = [];
          this.$api.alarm.findAll(params).then(res => {
            data = res.data.list;
            // data = this.tableData;
            this.tableData = this.getTableData(data);
            this.show = true;
          })
        },
        /*tableData*/
        getTableData(data) {
          if(data instanceof Array && data.length > 0) {
            data.forEach(item => {
              item.flag = true;
              /*if(item.status) {
                item.disposalStatus = item.status;
              }*/
              switch(item.disposalStatus) {
                case '1': item.disposalStatus = '';
                  break;
                case '2': item.disposalStatus = '';
                  break;
                case '3': item.disposalStatus = '';
                          item.flag = false;
                  break;
              }
              if(item.flag && item.category != '50' && !item.status) {
                item.dispatcher = item.dispatcher ? item.dispatcher : '';
                item.disposaler = item.disposaler ? item.disposaler : '';
                item.disposalChecker = item.disposalChecker ? item.disposalChecker : '';
                item.transferer = item.transferer ? item.transferer : '';
                item.transferDisposaler = item.transferDisposaler ? item.transferDisposaler : '';
                item.checker = item.checker ? item.checker : '';
              }else if(item.flag && item.category == '50' && !item.status) {
                item.dispatcher = item.dispatcher ? item.dispatcher : '';
                item.disposaler = item.disposaler ? item.disposaler : '';
                item.disposalChecker = item.disposalChecker ? item.disposalChecker : '';
                item.transferer = '';
                item.transferDisposaler = '';
                item.checker = '';
              }else if(item.status) {
                item.dispatcher = item.dispatcher ? item.dispatcher : '';
                item.disposaler = item.disposaler ? item.disposaler : '';
                item.disposalChecker = item.disposalChecker ? item.disposalChecker : '';
                item.transferer = item.transferer ? item.transferer : '';
                item.transferDisposaler = item.transferDisposaler ? item.transferDisposaler : '';
                item.checker = item.checker ? item.checker : '';
              }else {
                item.dispatcher = '';
                item.disposaler = '';
                item.disposalChecker = '';
                item.transferer = '';
                item.transferDisposaler = '';
                item.checker = '';
              }
            })
          }
          return data;
        },
        change(row) {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(()=>{
            this.$api.alarm.updateStatus(this.qs.stringify({id: row.id,disposalStatus: row.disposalStatus})).then(res => {
              if(res.status == '200'){
                this.$message({
                  type: 'success',
                  message: ''
                });
              }else{
                this.$message({
                  type: 'error',
                  message: ''
                });
              }
              this.searchAlarm();
            }).catch(()=>{
              this.$message({
                type: 'info',
                message: ''
              });
              this.searchAlarm();
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
            this.searchAlarm();
          })
        },
        /**/
        toWaterSupervision(row) {

        },
        /**/
        changeTimes(params) {
          this.start = params.start;
          this.end = params.end;
          this.show = false;
          this.searchAlarm();
        },
        /**/
        changeType(params) {
          this.type = params.type;
          this.show = false;
          this.searchAlarm();
        },
        /**/
        changeStatus(params) {
          if(params.status === '2'){
            this.show = false;
            this.name = 'status';
            this.value = params.status;
            this.disposalStatus = 1;
            this.searchAlarm();
          }else if(params.status === '0' || params.status === '1'){
            this.show = false;
            this.name = 'status';
            this.value = params.status;
            this.disposalStatus = 2;
            this.searchAlarm();
          }else if(params.status === '3'){
            this.show = false;
            this.name = 'status';
            this.value = '1';
            this.disposalStatus = 3;
            this.searchAlarm();
          }
        },
        /*OA/*/
        alarmHandle(row) {
          //this.dispatchFlag = true;
          //this.sheetFlag = true;
          //this.receiptFlag = true;
          //this.repairTransferFlag = true;
          //this.repairOrderFlag = true;
          //this.repairReceiptFlag = true;
          //this.faultDispatchFlag = true;
          //this.faultOrderFlag = true; // 
          //this.faultReceiptFlag = true; // 
          this.alarmId = row.id;
          let type = row.processStatus;
          switch(type) {
            case '100': this.dispatchFlag = true; //
            break;
            case '110': this.sheetFlag = true; //
            break;
            case '120': this.receiptFlag = true; // 
            break;
            case '200': this.repairTransferFlag = true; //
            break;
            case '210': this.repairOrderFlag = true; // 
            break;
            case '220': this.repairReceiptFlag = true; // 
            break;
            case '300': this.faultDispatchFlag = true; // 
            break;
            case '310': this.faultOrderFlag = true; // 
            break;
            case '320': this.faultReceiptFlag = true; // 
            break;
          }
        },
        /*OA/*/
        alarmPrint(type,row) {
          this.isOpen = true;
          this.status = type;
          this.printId = row.id;
        },
        /**/
        changePrint(params) {
          this.isOpen = params.isOpen;
        },
        openPrint() {
          this.status = 0;
        },
        /**/
        changeSurveyDispatch(params) {
          this.dispatchFlag = params.dispatchFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeSheetDispatch(params) {
          this.sheetFlag = params.sheetFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeSurveyReceipt(params) {
          this.receiptFlag = params.receiptFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeRepairTransfer(params) {
          this.repairTransferFlag = params.repairTransferFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeRepairOrder(params) {
          this.repairOrderFlag = params.repairOrderFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeRepairReceipt(params) {
          this.repairReceiptFlag = params.repairReceiptFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeFaultDispatch(params) {
          this.faultDispatchFlag = params.faultDispatchFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeFaultOrder(params) {
          this.faultOrderFlag = params.faultOrderFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        changeFaultReceipt(params) {
          this.faultReceiptFlag = params.faultReceiptFlag;
          if(params.type && params.type == 1) {
            this.searchAlarm();
          }
        },
        /**/
        exportData() {
          export2Excel(this.columns,this.tableData,this.title);
        },
        /**/
        link(row) {
          let url = '';
          switch(row.category) {
            case '10': url = 'WaterUseSupervision';
            break;
            case '20': url = 'BuildingWaterSupervision';
            break;
            case '30': url = 'DMAWaterSupervision';
            break;
            case '40': url = 'CampusWaterSupervision';
            break;
            case '50': url = 'WaterUseSupervision';
            break;
          }
          this.$router.push({
            name: url,
            params:{
              id : Number(row.id.substring(1,4))
            }
          })
        }
      },
    }
</script>

<style scoped>
  @import "../../../static/css/alarmOAManagement.css";
  .el-select {width: 100px;padding-bottom: 5px;padding-top:3px;}
  .yellow {background: yellow;height: 100%;width: 100%;}
  .green {background: green;height: 100%;width: 100%;color: white;}

  .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 15%;
  }
  .redItem .el-form-item__label{
    color: #A0A0A0;
  }
  .el-divider {
    margin: 1px 0;
  }
</style>



